export default {
  props: ["boardData"],
  mounted() {},
  data() {
    return {
      collab: {
        id: this.boardData.id,
        collabEmail: this.boardData.collabEmail
      },
      collaborator: ""
    };
  },
  computed: {
    board() {
      return this.$store.state.activeBoard;
    }
  },
  methods: {
    async addCollab() {
      await this.collab.collabEmail.push(this.collaborator);
      await this.$store.dispatch("editBoard", this.collab);
      this.collaborator = "";
    }
  }
};
