export default {
  data() {
    return {
      newList: {
        boardId: this.$route.params.boardId,
        authorId: this.$auth.userInfo.email
      }
    };
  },
  methods: {
    async CreateList() {
      await this.$store.dispatch("createList", this.newList);
      console.log(this.newList);
      this.newList.title = "";
    }
  }
};
