export default {
  data() {
    return {
      newTask: {
        listId: this.listId,
        boardId: this.boardId,
        authorId: this.$auth.userInfo.email
      },
      form: false
    };
  },
  methods: {
    async CreateTask() {
      console.log(this.newTask);
      await this.$store.dispatch("createTask", this.newTask);
      this.newTask.title = "";
      this.form = false;
    }
  },
  props: ["listId", "boardId"]
};
